import React, { useContext, useEffect } from 'react'
import classes from './Works.module.css'
import {WorkContext} from "../../Context/Work/WorkContext";


const Works = () => {
    const {works, changeCurrentWork, currentWork} = useContext(WorkContext);

    useEffect(() => {
        const activeLink = document.querySelector(`.${classes.card}.${classes.active}`);
        activeLink.scrollIntoView({inline: "center", behavior: "smooth"});
    }, []);

    const projects = works.map(work => {
        const cls = [classes.card];

        if (work.id === currentWork) cls.push(classes.active)

        const image = require(`../../img/works/${work.id}.png`);

        return (
            <div onClick={() => changeCurrentWork(work.id)}
                 style={{backgroundImage: `url(${image})`}}
                 className={cls.join(' ')}
                 key={work.id}
            >
                <span data-title={work.title}>{work.title}</span>
            </div>
        )
    });

    return (
        <div className={classes.Works}>
            { projects }
        </div>
    )
}

export default Works