import React from 'react'
import classes from "./Skills.module.css";

const Table = props => {
    const technologiesList = props.technologies.map((t ,id) => (
        <div className={classes.texnology} key={id}>
            <div className={classes.name}>{ t.name }</div>
            <div className={classes.progressBar}>
                <div className={classes.cell + ' ' + (t.level >= 1 ? classes.l1 : null)}></div>
                <div className={classes.cell + ' ' + (t.level >= 2 ? classes.l2 : null)}></div>
                <div className={classes.cell + ' ' + (t.level >= 3 ? classes.l3 : null)}></div>
            </div>
        </div>
    ));

    return (
        <div className={classes.table}>
            { technologiesList }
        </div>
    )
};

export default Table