import {CHANGE_CURRENT_WORK} from "../Types";


const handlers = {
    [CHANGE_CURRENT_WORK]: (state, {payload}) => ({...state, currentWork: payload}),
    DEFAULT: state => state
}

export const WorkReducer = (state, action) => {
    const handler = handlers[action.type] || handlers.DEFAULT;
    return handler(state, action);
}

