import React, { useContext, useEffect } from 'react'
import classes from './WorkInfo.module.css'
import {WorkContext} from "../../Context/Work/WorkContext";
import {NavContext} from "../../Context/Nav/NavContext";

const WorkInfo = () => {
    const { nav } = useContext(NavContext);
    const {currentWork, works} = useContext(WorkContext);

    useEffect(() => {
        const workContainer = document.querySelector(`.${classes.CurrentWork}`);
       workContainer.scrollTop = 0;
    }, [currentWork]);

    const id = works.findIndex(w => w.id === currentWork);
    const work = works[id];

    const listTasks = work.tasks[nav.lang].map(task => {
        return (
            <li key={task.id}> { task.text } </li>
        )
    });

    const commentParagraphs = work.comments[nav.lang].map((comment, idx) => (
        <p key={idx}>{comment}</p>
    ));

    const text = {
        ru: {
            comment: 'Комментарий:',
            task: 'Решённые задачи:',
            technology: 'Основные технологии:',
            commercial: 'Комерческий:',
            link: 'Ссылка на проект',
            yes: 'да',
            no: 'нет',
        },
        en: {
            comment: 'Comment:',
            task: 'Solved Tasks:',
            technology: 'Core technology:',
            commercial: 'Commercial:',
            link: 'Link to the project',
            yes: 'yes',
            no: 'no',
        },
    };

    const com = (<span>{ work.comertical ? text[nav.lang].yes : text[nav.lang].no }</span>);

    return (
        <div className={classes.CurrentWork}>
            <div className={classes.title}>{ work.title }</div>
            <div>{ text[nav.lang].comment }</div>
            <div className={classes.comment}>{ commentParagraphs }</div>
            <div className={classes.tasks}>{ text[nav.lang].task }</div>
            <ol>{ listTasks }</ol>
            <div>{ text[nav.lang].technology } { work.technologies.join(', ') } </div>
            <div>{ text[nav.lang].commercial } { com }</div>
            <a href={work.link} className={classes.link} target="_blank" rel="noopener noreferrer"
            >- { text[nav.lang].link } -</a>
        </div>
    )
};

export default WorkInfo;