import React, {useContext} from 'react'
import classes from './Nav.module.css'
import { NavContext } from '../../../Context/Nav/NavContext'

const Nav = (props) => {
    const { nav, setComponent, toggleLanguage } = useContext(NavContext);

    const id = nav.currentComponent;

    const changeComponent = (id) => {
        props.changeImage(nav.currentComponent, id);
        setComponent(id);
    };

    const navigation = nav.components[nav.lang].map(li => (
        <li onClick={() => changeComponent(li.id)}
            className={ id === li.id ? classes.active : null }
            key={li.id}>{ li.text }</li>
    ));

    return (
        <ul className={classes.Nav}>
            { navigation }
            <li onClick={toggleLanguage}>RU/EN</li>
        </ul>
    )
};

export default Nav