import * as THREE from 'three';
import { TimelineMax, Elastic } from 'gsap';

export default class ImageCreator {
    constructor(selector, images) {
        this.scene = new THREE.Scene();
        this.camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
        this.camera.position.set(0, 0, 1);

        this.renderer = new THREE.WebGLRenderer();
        this.renderer.setSize(window.innerWidth, window.innerHeight);

        this.container = document.querySelector(selector);
        this.container.appendChild(this.renderer.domElement);

        this.images = images;

        this.w = window.innerWidth;
        this.h = window.innerHeight;
        this.scene.destination = {x: 0, y: 0};

        this.t = new TimelineMax();
        this.time = 0;

        this.setupResize();
        this.resize();

        this.addObject();
        this.animate();
        this.setupMouseMove();
    }

    setCameraPositionZ(coordinate) {
        this.camera.position.set(0, 0, coordinate);
    }

    setupResize() {
        window.addEventListener('resize', this.resize.bind(this));
    }

    resize() {
        const width = parseInt(window.getComputedStyle(this.container).width);
        const height = parseInt(window.getComputedStyle(this.container).height);
        this.renderer.setSize(width, height);
        this.camera.aspect = width / height;
        this.camera.updateProjectionMatrix();
        if (window.innerWidth > 1440) {
            this.setCameraPositionZ(1);
        } else if (window.innerWidth > 1024) {
            this.setCameraPositionZ(1.35);
        } else if (window.innerWidth > 900) {
            this.setCameraPositionZ(1.75);
        } else if (window.innerWidth > 768) {
            this.setCameraPositionZ(1.5);
        } else if (window.innerWidth > 420) {
            this.setCameraPositionZ(1.75);
        }
    }

    render() {
        this.material.uniforms.time.value = this.time;
        this.scene.rotation.x += (this.scene.destination.x - this.scene.rotation.x) * 0.05;
        this.scene.rotation.y += (this.scene.destination.y - this.scene.rotation.y) * 0.05;
        this.renderer.render(this.scene, this.camera);
    }

    addObject() {
        this.geometry = new THREE.PlaneGeometry(1, 1, 200, 200);

        const firstTexture = new THREE.TextureLoader().load(this.images[0]);
        const secondTexture = new THREE.TextureLoader().load(this.images[1]);
        const thirdTexture = new THREE.TextureLoader().load(this.images[2]);

        this.material = new THREE.ShaderMaterial({
            extensions: {
                derivatives: '#extension GL_OES_standard_derivatives : enable',
            },
            uniforms: {
                elastic: {type: 'f', value: 0},
                time: {type: 'f', value: this.time},
                blend: {type: 'f', value: 0},
                active: {type: 'f', value: 1},
                nextActive: {type: 'f', value: 2},
                firstImage: {type: 't', value: firstTexture},
                secondImage: {type: 't', value: secondTexture},
                thirdImage: {type: 't', value: thirdTexture},
            },
            vertexShader: this.getShader('vert'),
            fragmentShader: this.getShader('frag'),
        });

        this.points = new THREE.Mesh(this.geometry, this.material);
        this.scene.add(this.points);
    }

    animate() {
        if (this.time <= 25) {
            this.time += 0.15;
        }
        requestAnimationFrame(this.animate.bind(this));
        this.render();
    }

    mouseMove(e) {
        const x = (e.clientX - this.w / 2) / (this.w / 2);
        const y = (e.clientY - this.h / 2) / (this.h / 2);
        this.scene.destination.x = y * 0.5;
        this.scene.destination.y = x * 0.5;
    }

    setupMouseMove() {
        window.addEventListener('mousemove', this.mouseMove.bind(this));
    }

    changeSmile(active, nextActive) {
        this.material.uniforms.active.value = active;
        this.material.uniforms.nextActive.value = nextActive;
        this.material.uniforms.blend.value = 0;
        this.t.to(this.material.uniforms.blend, 1, {
            value: 1,
        });
    }

    createVolume(value = 0.2) {
            this.t.to(this.material.uniforms.elastic, 3, {
                value,
                ease: Elastic.easeOut.config(0.2, 0.05),
            });
    }

    getShader(type) {
        if (type === 'frag') {
            return `varying vec3 v_position;
                    varying vec2 vUv;
                    uniform sampler2D firstImage;
                    uniform sampler2D secondImage;
                    uniform sampler2D thirdImage;
                    uniform float blend;
                    uniform float active;
                    uniform float nextActive;
                    uniform float time;
                    
                    void main() {
                        vec2 st = vec2(v_position.x, v_position.y * 0.025 * time);
                        float koef = clamp(v_position.z/60.,0.,1.);
                    
                        vec3 color1 = vec3(1.,1.,1.);
                        vec3 color2 = vec3(1.,0.,0.);
                    
                        vec3 color3 = mix(color1,color2,koef);
                    
                        vec2 grid = abs(fract(500.*st/5.) - 0.5) / fwidth(500.*st/4.);
                        float color = min(1., grid.y);
                    
                        gl_FragColor = vec4(color3,1. - color);
                        gl_FragColor = vec4(1.,0.,0.,1.);
                        
                        vec4 firstImageColor = texture2D(firstImage,vUv);
                        vec4 secondImageColor = texture2D(secondImage,vUv);
                        vec4 thirdImageColor = texture2D(thirdImage,vUv);
                        
                        vec4 currentImageColor;
                        
                        if (active == 1.) {
                          currentImageColor = firstImageColor;
                        } else if (active == 2.) {
                          currentImageColor = secondImageColor;
                        } else if (active == 3.) {
                          currentImageColor = thirdImageColor;
                        }
                        
                        vec4 nextImageColor;
                        
                        if (nextActive == 1.) {
                          nextImageColor = firstImageColor;
                        } else if (nextActive == 2.) {
                          nextImageColor = secondImageColor;
                        } else if (nextActive == 3.) {
                          nextImageColor = thirdImageColor;
                        }
                        
                        vec4 result = mix(currentImageColor, nextImageColor, blend);
                    
                        gl_FragColor = result*(1. - color);
                    }`
        } else {
            return `varying vec2 vUv;
                    varying vec3 vecPos;
                    varying vec3 v_position;
                    uniform sampler2D firstImage;
                    uniform sampler2D secondImage;
                    uniform sampler2D thirdImage;
                    uniform float elastic;
                    uniform float blend;
                    uniform float active;
                    uniform float nextActive;
                    
                    
                    void main() {
                        vUv = uv;
                        v_position = position.xyz;
                    
                        float firstTexture = texture2D(firstImage, vUv).r;
                        float secondTexture = texture2D(secondImage, vUv).r;
                        float thirdTexture = texture2D(thirdImage, vUv).r;
                        
                        
                        float currentImageTexture;
                        
                        if (active == 1.) {
                          currentImageTexture = firstTexture;
                        } else if (active == 2.) {
                          currentImageTexture = secondTexture;
                        } else if (active == 3.) {
                          currentImageTexture = thirdTexture;
                        }
                        
                        float nextImageTexture;
                        
                        if (nextActive == 1.) {
                          nextImageTexture = firstTexture;
                        } else if (nextActive == 2.) {
                          nextImageTexture = secondTexture;
                        } else if (nextActive == 3.) {
                          nextImageTexture = thirdTexture;
                        }
                        
                        v_position.z = elastic * mix(currentImageTexture,nextImageTexture, blend);
                        
                        vecPos = (modelViewMatrix * vec4(v_position, 1.0)).xyz;
                        gl_Position = projectionMatrix * vec4(vecPos, 1.0);
                    }`
        }
    }
}
