import React, { useContext } from 'react'
import classes from './About.module.css'
import { NavContext } from "../../Context/Nav/NavContext";

const text = {
    ru: {
        title: 'Привет, меня зовут Ярослав Емельянов',
        subtitle: 'Рад видеть тебя здесь!',
        about: `Здесь представлены некоторые мои работы,
                которые мне визуально или логически привлекательны.
                Эти проекты моя возможность показать, что у меня есть опыт разработки,
                есть желание развиваться, изучать и мне это реально интересно.`
    },
    en: {
        title: 'Hi, my name is Yaroslav Emelyanov',
        subtitle: 'Good to see you here!',
        about: `Here are some of my works,
                which are visually or logically attractive to me.
                These projects are my opportunity to show that I have development experience,
                I have a desire to develop, to learn, and it is really interesting to me.`
    },
};

const About = props => {
    const { nav } = useContext(NavContext);

    const title = (
            <div className={classes.title}>
                { text[nav.lang || 'ru'].title }
            </div>
    );

    return (
        <div className={classes.About}>
            <div>
                { title }
                <div className={classes.subtitle}>{ text[nav.lang || 'ru'].subtitle }</div>
            </div>
            <div className={classes.text}>{ text[nav.lang || 'ru'].about }</div>
        </div>
    );
};

export default About;