import React, {useContext, useEffect, useState} from 'react';
import './App.css'
import Nav from './components/UI/Nav/Nav'
import Content from './hoc/Content/Content'
import Smile from './components/Smile/Smile'
import WorkInfo from './components/WorkInfo/WorkInfo'
import { NavContext } from './Context/Nav/NavContext';
import Contacts from "./components/Contacts/Contacts";
import imageCreator from "./components/Smile/imageCreator";

const App = () => {
    const { nav } = useContext(NavContext);
    const [image, setImage] = useState(null);

    useEffect(() => {
        const ghost = require(`./img/emoji/ghost.png`);
        const est = require(`./img/emoji/est.png`);
        const devil = require(`./img/emoji/devil.png`);
        const image = new imageCreator('.Smile', [devil, ghost, est]);
        setImage(image);
        setTimeout(() => {
            image.createVolume();
        }, 1000);
        // eslint-disable-next-line
    }, []);

    return (
      <div className="App">
        <Smile/>
        { nav.currentComponent === 2 && <WorkInfo/> }
        <Content/>
        <Nav changeImage={(active, nextActive) => {image.changeSmile(active, nextActive)}}/>
        <Contacts/>
      </div>
    )
};

export default App;
