import {TOGGLE_LANGUAGE, SET_CONTENT_COMPONENT} from "../Types"

const handlers = {
    [SET_CONTENT_COMPONENT]: (state, {payload}) => ({...state, currentComponent: payload}),
    [TOGGLE_LANGUAGE]: (state, {payload}) => ({...state, lang: payload}),
    DEFAULT: state => state
}

export const NavReducer = (state, action) => {
      const handler = handlers[action.type] || handlers.DEFAULT;
      return handler(state, action)
}