import React, {useReducer} from 'react'
import {NavReducer} from './NavReducer'
import { NavContext } from './NavContext'
import {TOGGLE_LANGUAGE, SET_CONTENT_COMPONENT} from '../Types'

export const NavState = ({children}) => {
    const initialState = {
        lang: 'ru',
        components : {
            ru: [
                {id: 1, text: 'Обо мне'},
                {id: 2, text: 'Мои работы'},
                {id: 3, text: 'Навыки && Опыт'},
            ],
            en: [
                {id: 1, text: 'About'},
                {id: 2, text: 'Works'},
                {id: 3, text: 'Skills && Expirience'},
            ]
        },
        currentComponent: 1,
        work: 1
    };

    const [state, dispatch] = useReducer(NavReducer, initialState);

    const setComponent = id => dispatch({type: SET_CONTENT_COMPONENT, payload: id});
    const toggleLanguage = () => {
        const lang = state.lang === 'ru' ? 'en' : 'ru';
        dispatch({type: TOGGLE_LANGUAGE, payload: lang});
    };


    return (
        <NavContext.Provider value={{
            nav: state, setComponent, toggleLanguage,
        }}>
            {children}
        </NavContext.Provider>
    )
};

