import React, {useContext} from 'react'
import classes from './Skills.module.css'
import HeaderTable from "./headerTable";
import Table from "./Table";
import FooterTable from "./footerTable";
import {NavContext} from "../../Context/Nav/NavContext";

const headerColumns = {
    ru: [ 'технология', 'новичок', 'средний', 'опытный'],
    en: [ 'technology', 'beginner', 'averange', 'skilled']
};


const technologies = [
    {name: 'html', level: 3},
    {name: 'css', level: 3},
    {name: 'scss/sass', level: 3},
    {name: 'js', level: 3},
    {name: 'vue js', level: 3},
    {name: 'nuxt js', level: 3},
    {name: 'react js', level: 2},
    {name: 'node js', level: 3},
    {name: 'php', level: 1},
    {name: 'webpack', level: 2},
    {name: 'gulp', level: 1},
    {name: 'git', level: 2},
    {name: 'docker', level: 0},
    {name: 'sql', level: 3},
    {name: 'jquery', level: 2},
    {name: 'typescript', level: 1},
    {name: 'firebase', level: 2},
    {name: 'mongo db', level: 3},
    {name: 'python', level: 0},
    {name: 'glsl', level: 1},
];

const Skills = () => {
    const { nav } = useContext(NavContext);

    return (
        <div className={classes.Skills}>
            <HeaderTable columns={headerColumns} lang={nav.lang}/>
            <Table technologies={technologies} lang={nav.lang}/>
            <FooterTable lang={nav.lang} className={classes.footer}/>
        </div>
    );
}

export default Skills